@charset "UTF-8";
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --color-primary: #eb2f64;
  --color-primary-light: #FF3366;
  --color-primary-dark: #BA265D;
  --color-grey-light-1: #faf9f9;
  --color-grey-light-2: #f4f2f2;
  --color-grey-light-3: #f0eeee;
  --color-grey-light-4: #ccc;
  --color-grey-dark-1: #333;
  --color-grey-dark-2: #777;
  --color-grey-dark-3: #999;
  --shadow-dark: 0 2rem 6rem (0, 0, 0, .3);
  --shadow-light: 0 2rem 5rem (0, 0, 0, .06);
}

html {
  font-size: 62.5%;
}

body {
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  line-height: 1.6;
  color: var(--color-grey-dark-2);
  background-image: linear-gradient(to right bottom, var(--color-primary-light), var(--color-primary-dark));
  background-size: cover;
  min-height: 100vh;
}

@keyframes pulsate {
  0% {
    transform: scale(1);
    box-shadow: none;
  }
  50% {
    transform: scale(1.05);
    box-shadow: 0 1rem 4rem rgba(0, 0, 0, 0.25);
  }
  100% {
    transform: scale(1);
    box-shadow: none;
  }
}
.container {
  max-width: 120rem;
  margin: 8rem auto;
  background-color: var(--color-grey-light-1);
  box-shadow: var(--shadow-dark);
  min-height: 50rem;
}

.header {
  background-color: #fff;
  font-size: 1.4rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 7rem;
  border-bottom: 1px solid var(--color-grey-light-2);
}

.content {
  display: flex;
}

.sidebar {
  background-color: var(--color-grey-dark-1);
  flex: 0 0 18%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.preview {
  background-color: #fff;
  flex: 1;
}

.details {
  display: flex;
  padding: 4.5rem;
  background-color: var(--color-grey-light-1);
  border-bottom: 1px solid var(--color-grey-light-2);
  font-size: 1.4rem;
}

.description {
  flex: 0 0 60%;
  background-color: #fff;
  box-shadow: var(--shadow-light);
  padding: 3rem;
  margin-right: 4.5rem;
}

.reviews {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.logo {
  height: 3.25rem;
  margin-left: 2rem;
}

.search {
  flex: 0 0 40%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.search__input {
  width: 90%;
  border: none;
  background-color: var(--color-grey-light-2);
  font-family: inherit;
  font-size: inherit;
  border-radius: 1000px;
  padding: 0.7rem 2rem;
  transition: all 0.2s;
  margin-right: -3.25rem;
}
.search__input:focus {
  width: 100%;
  background-color: var(--color-grey-light-3);
  outline: none;
}
.search__input::placeholder {
  font-weight: 100;
  color: var(--color-grey-light-4);
}
.search__input:focus + .search__button {
  background-color: var(--color-grey-light-3);
}
.search__button {
  background-color: var(--color-grey-light-2);
  border: none;
}
.search__button:active {
  transform: translateY(2px);
}
.search__icon {
  height: 2rem;
  width: 2rem;
  fill: var(--color-grey-dark-3);
}

.user-nav {
  display: flex;
  align-self: stretch;
}
.user-nav > * {
  display: flex;
  align-items: center;
  padding: 0 2rem;
  cursor: pointer;
}
.user-nav > *:hover {
  background-color: var(--color-grey-light-2);
}
.user-nav__icon {
  height: 2.25rem;
  width: 2.25rem;
  fill: var(--color-grey-dark-2);
}
.user-nav__icon-box {
  position: relative;
}
.user-nav__notification {
  font-size: 0.8rem;
  width: 1.75rem;
  height: 1.75rem;
  border-radius: 50%;
  color: #fff;
  background-color: var(--color-primary);
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 1.5rem;
  right: 1.1rem;
}
.user-nav__avatar {
  height: 3.75rem;
  border-radius: 50%;
  margin-right: 1rem;
}

.side-nav {
  list-style: none;
  font-size: 1.5rem;
  margin-top: 3.5rem;
}
.side-nav__link:link, .side-nav__link:visited {
  text-transform: uppercase;
  text-decoration: none;
  color: var(--color-grey-light-1);
  display: block;
  padding: 1.5rem 3rem;
  position: relative;
  z-index: 22;
}
.side-nav__icon {
  width: 1.75rem;
  height: 1.75rem;
  fill: currentColor;
  margin-right: 2rem;
}
.side-nav__item {
  position: relative;
}
.side-nav__item:not(:last-child) {
  margin-bottom: 0.5rem;
}
.side-nav__item::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 3px;
  height: 100%;
  background-color: var(--color-primary);
  transform: scaleY(0);
  transition: transform 0.2s, width 0.4s cubic-bezier(1, 0, 0, 1) 0.2s, background-color 0.1s;
}
.side-nav__item:hover::before, .side-nav__item--active::before {
  width: 100%;
  transform: scaleY(1);
}
.side-nav__item:active::before {
  background-color: var(--color-primary-light);
}

.legal {
  font-size: 1.2rem;
  text-align: center;
  padding: 2.5rem;
  color: var(--color-grey-light-4);
}

.gallery {
  display: flex;
  align-items: center;
}
.gallery__img {
  width: 100%;
  display: block;
}

.overview {
  display: flex;
  align-items: center;
  border-bottom: 1px solid var(--color-grey-light-2);
}
.overview__title {
  font-size: 2.25rem;
  font-weight: 300;
  letter-spacing: 1px;
  padding: 1.5rem 3rem;
  text-transform: uppercase;
}
.overview__icon-star, .overview__icon-location {
  width: 1.75rem;
  height: 1.75rem;
  fill: var(--color-primary);
}
.overview__stars {
  margin-right: auto;
}
.overview__location {
  font-size: 1.2rem;
  display: flex;
  align-items: center;
  margin-right: 3rem;
}
.overview__icon-location {
  margin-right: 0.5rem;
}
.overview__ratings {
  background-color: var(--color-primary);
  color: #fff;
  padding: 0 2.25rem;
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.overview__rating {
  font-size: 2.25rem;
  font-weight: 300;
  margin-bottom: -3px;
}
.overview__value {
  font-size: 0.8rem;
}

.button-inline {
  font-size: inherit;
  color: var(--color-primary);
  border: none;
  background-color: transparent;
  border-bottom: 1px solid currentColor;
  padding-bottom: 2px;
  cursor: pointer;
}
.button-inline span {
  margin-left: 3px;
  transition: margin-left 0.2s;
}
.button-inline:hover {
  color: var(--color-grey-dark-1);
}
.button-inline:hover span {
  margin-left: 8px;
}
.button-inline:focus {
  animation: pulsate 1s infinite;
}

.paragraph:not(:last-of-type) {
  margin-bottom: 2rem;
}

.list {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  border-top: 1px solid var(--color-grey-light-2);
  border-bottom: 1px solid var(--color-grey-light-2);
  margin: 3rem 0;
  padding: 3rem 0;
}
.list__item {
  flex: 0 0 50%;
  margin-bottom: 0.7rem;
}
.list__item::before {
  content: "";
  width: 1rem;
  height: 1rem;
  -webkit-mask-image: url(../img/chevron-thin-right.svg);
  -webkit-mask-size: cover;
  mask-image: url(../img/chevron-thin-right.svg);
  mask-size: cover;
  display: inline-block;
  margin-right: 0.7rem;
  background-color: var(--color-primary);
}

.recommend {
  display: flex;
  align-items: center;
  font-size: 1.3rem;
  color: var(--color-grey-dark-3);
}
.recommend__count {
  margin-right: auto;
}
.recommend__img {
  height: 4rem;
  width: 4rem;
  border-radius: 50%;
  box-sizing: content-box;
  border: 3px solid #fff;
}
.recommend__img:not(:last-child) {
  margin-right: -1.5rem;
}

.review {
  background-color: #fff;
  box-shadow: var(--shadow-light);
  padding: 3rem;
  margin-bottom: 3.5rem;
  position: relative;
  overflow: hidden;
}
.review::before {
  content: "â€œ";
  line-height: 1;
  position: absolute;
  top: -2.75rem;
  left: -1rem;
  font-size: 20rem;
  font-family: sans-serif;
  color: var(--color-grey-light-2);
  z-index: 1;
}
.review__text {
  margin-bottom: 2rem;
  position: relative;
  z-index: 2;
}
.review__box {
  display: flex;
  align-items: center;
}
.review__img {
  width: 4.5rem;
  height: 4.5rem;
  border-radius: 50%;
  margin-right: 1.5rem;
}
.review__user {
  margin-right: auto;
}
.review__user-name {
  font-size: 1.1rem;
  font-weight: 600;
  margin-bottom: 0.4rem;
}
.review__user-date {
  font-size: 1rem;
  color: var(--color-grey-dark-3);
}
.review__rating {
  font-size: 2.2rem;
  font-weight: 300;
  color: var(--color-primary);
}